/*
 * {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
} */

html, body, #root {
  height: 100%;
}



body, input, button {
  font-family: Arial, Helvetica, sans-serif;
}

/* ############# */
h1 {
  color: green;
  font-weight: 900;
  text-transform: uppercase;
}

ul {
  list-style: none;
}

.note {
  color: grey;
  padding-top: 9px;
  font-size: 15px;
}

.error {
  color: white;
  background:lightcoral;
  font-size: 20px;

  border: 2px solid red;
  border-radius: 8px;

  padding: 5px;
  margin-bottom: 10px;
}
